/**
 * Logs in a user by calling your Go backend.
 * The backend will set a secure HttpOnly cookie on success.
 * @param {string} email - The user's email.
 * @param {string} password - The user's password.
 */
export async function login(email, password) {
    const res = await fetch(`${"http://localhost:8000"}/auth/login`, {
        method: "POST",
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password }),
    });

    if (!res.ok) {
        // If the server responds with an error status (401, 404, 500), throw an error.
        const errorData = await res.json();
        throw new Error(errorData.message || 'Login failed');
    }

    // If login is successful, the browser will have the cookie.
    return res.json();
}